*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
  display: flex;
  flex: 1;
  flex-direction: column;
  overflow-y: scroll;
  padding: 0;
  margin: 0;
}

.wrapper {
  min-height: 100vh;
}

html {
  font-family: 'Roboto', Helvetica, sans-serif;
  font-size: 100%;
  font-style: normal;
  font-weight: normal;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

body {
  color: #111;
  line-height: inherit;
}

h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 0.8rem;
}

h5,
h6 {
  margin-top: 4rem;
  font-size: 1.25rem;
  line-height: 2rem;
}

h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 5rem;
  line-height: 8rem;
}

h2 {
  margin-top: 4rem;
  font-size: 2.5rem;
  line-height: 3rem;
}

h3 {
  margin-top: 3.2rem;
  font-size: 1.79688rem;
  line-height: 2rem;
}

h4 {
  margin-top: 2.375rem;
  font-size: 1.5rem;
  line-height: 2rem;
}

h1 + h2 {
  margin-top: 1.6rem;
}

h2 + h3,
h3 + h4,
h4 + h5 {
  margin-top: 0.8rem;
}

h5 + h6 {
  margin-top: -0.8rem;
}

small,
.text--small {
  font-size: 80%;
}

a {
  color: rgba(0, 0, 0, 0.9);
  text-decoration: none;
  cursor: pointer;
}

a:hover {
  color: black;
}

a.active {
  color: black;
}

.btn {
  position: relative;
  display: inline-block;
  padding-left: 0.75rem;
  font-weight: 400;
  line-height: 1.25;
  padding: 1rem 1.375rem;
  font-size: 1.125rem;
  font-weight: 600;
  color: #fff;
  background: #111;
  border: 2px solid #000;
}

.btn:hover {
  color: #111;
  background: #fff;
}

.header {
  display: flex;
  flex: 0 1 auto;
  align-items: center;
  padding: 0 24px;
  width: 100%;
  height: 60px;
}

footer {
  padding: 1.125rem 0;
  width: 100%;
  font-size: 0.8125rem;
  color: #666;
  text-align: center;
  border-top: 1px solid #eee;
}

footer a {
  margin-left: .5ch;
}

footer a:first-child {
  margin-right: .5ch;
}

nav {
  display: flex;
  flex: 1;
}

nav a {
  margin-right: 24px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.5);
}

nav a:hover {
  color: black;
}

nav .active {
  color: black;
}

.home main {
  padding: 3rem 1.5rem;
}

@media screen and (min-width: 961px) {
  .home main {
    margin: 0 auto;
    width: 100%;
    max-width: 60rem;
  }
}

.page__top {
  padding-top: 6rem;
  padding-bottom: 6rem;
  text-align: center;
}

.page__top h1 {
  margin-bottom: 0.75rem;
  font-size: 2rem;
  line-height: 1.6;
}

.page__top .btn {
  margin-top: 0.75rem;
  margin-right: auto;
  margin-left: auto;
}

.boilerplate-logo {
  clear: both;
  display: block;
  flex: none;
  overflow: hidden;
  margin-bottom: 1.5rem;
  width: 100%;
  height: 7rem;
  background: url(./../images/bull-icon.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 7rem;
}

.main__header {
  padding-bottom: 72px;
  width: 100%;
  color: #000;
  text-align: center;
}

hr {
  margin: 24px auto 0;
  max-width: 96px;
  height: 1px;
  color: #ddd;
  background-color: #ddd;
  border: none;
}

.main__content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}

.main__item {
  display: flex;
  flex: 0 1 auto;
  flex-flow: column nowrap;
  width: 100%;
  text-align: center;
}

@media (min-width: 555px) {
  .main__item {
    max-width: calc(50% - 48px);
  }

  .main__item:nth-of-type(2n+2) {
    margin-right: 3rem;
    margin-left: 3rem;
  }
}

@media (min-width: 555px) and (max-width: 991px) {
  .main__item:nth-of-type(n+3) {
    margin-top: 3rem;
  }
}

@media (min-width: 992px) {
  .main__item {
    max-width: calc(33.33333% - 32px);
  }

  .main__item:nth-of-type(2) {
    margin-right: 3rem;
    margin-left: 3rem;
  }

  .main__item:last-of-type {
    margin-top: 1.625rem;
  }
}

.main__item h4 {
  margin-top: 1rem;
}

.main__item-logo {
  display: block;
  margin-right: auto;
  margin-left: auto;
  width: 3rem;
  height: 3rem;
  background: url(./../images/react-logo.png);
  background-repeat: no-repeat;
  background-size: contain;
}

.main__item-logo.mobx {
  background: url(./../images/mobx-logo.png);
  background-repeat: no-repeat;
  background-size: contain;
}

.main__item-logo.reactrouter {
  background: url(./../images/react-router-logo.png);
  background-repeat: no-repeat;
  background-size: contain;
}

.main__item-logo.webpack {
  background: url(./../images/webpack-logo.png);
  background-repeat: no-repeat;
  background-size: contain;
}

.main__extras {
  text-align: center;
}

.main__extras ul {
  padding: 0;
  margin: 1.5rem 0 0;
  list-style-type: none;
}

